  color: var(--font-color-1);
  font-size: 25px;
}
.style_cardsWrapper__Sz9nx {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  gap: 20px;
  padding: 10px;
}

.style_cardWrapper__2HBDY {
  border-radius: 5px;
  display: grid;
  place-items: center;
}

.style_productCardWrapper__2jO0U iframe {
  min-height: 240px;
  border: 0;
  width: 120px;
  -webkit-transform: scaleX(1.4);
          transform: scaleX(1.4);
}

@media screen and (max-width: 350px) {
  .style_productCardWrapper__2jO0U iframe {
    -webkit-transform: scaleX(1.1);
            transform: scaleX(1.1);
